@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600&display=swap);
.suggestion {
    display: inline-block;
    position: relative;
    width: 25vw;
    height: 30vw;
    border: 2px solid white;
    border-radius: 4px;
    margin-top: 10%;
    margin-left: 5%;
}

#prod {
    width: 100%;
    height: 100%;
    position: relative;
    top: 10px;
    left: -10px;
    border-radius: 4px;
    background-color: white;
    object-fit: cover;
}

:root {
  --text-black: #161211;
  --light-pink: #FFE2DB;
  --gray: #D6E1DD;
  --green: #6E974F;
  --yellow: #F9CC12;
}

html {
  font-size: 16px;
}

body {
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  /* background-color: #2D3436; */
  background-color: white;
}

.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  padding: 2rem;
  width: 75%;
  margin: 0 auto;
}

.app-container.home {
  flex-direction: row;
}

#title-text {
  width: 55%;
  padding-right: 6rem;
}

#chat {
  flex-grow: 1;
  position: relative;
  height: 80%;
}

#chat .conversational-form {
  /* display: flex; */
  /* align-content: center; */
  /* justify-content: center; */
  box-shadow: 0 2px 28px 0 rgba(0,0,0,0.12);
}

.conversational-form cf-input textarea:focus,  .conversational-form cf-input input:focus{
  box-shadow: 0 0 0 1px rgba(0,0,0);
}

#welcome-message {
  color: var(--black);
  margin-left: 2.2rem;
  width: 400px;
}

#spin {
  display: inline-block;
  width: 1rem;
  -webkit-animation-name: spin;
          animation-name: spin;
  -webkit-animation-duration: 4000ms;
          animation-duration: 4000ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}


@-webkit-keyframes spin {
  from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
  to {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}


@keyframes spin {
  from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
  to {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}

#everwear-logo {
  object-fit: contain;
  width: 400px;
}

.results-list {
  margin: 0 auto;
  margin-top: 100px;
  width: 70%;
}

.results-list__title {
  padding-bottom: 1rem;
  margin-bottom: 3rem;
  border-bottom: 1px solid var(--green);
  display: inline-block;
}

.results-list__og {
  vertical-align: text-bottom;
  width: 4rem;
  margin: 0 1rem;
}

.result-list__siteLink {
  text-decoration: none;
  color: var(--black);
}

.result-list__siteLink:hover {
  text-decoration: underline;
  color: var(--black);
}


.results-list__list, .results-list__row {
  list-style: none;
  padding: 0;
}

.results-list__row {
  padding-bottom: 4rem;
  display: flex;
  flex-direction: row;
}

.results-list__item {
  width: 200px;
  margin-right: 1rem;
  background-color: white;
  /* border: 1px solid rgba(229, 230, 234, .5); */
}

.results-list__item:hover {
  background-color: var(--gray);
  -webkit-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}


.results-list__item:hover img{
  top: -4px;
  left: 4px;
  box-shadow: 0 2px 28px 0 rgba(0,0,0,0.2);
  border: 1px solid var(--gray);
  -webkit-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}

.results-list__item a {
  display: block;
  color: inherit;
}

.results-list__item__title {
  margin-top: 10px;
}

.results-list__item__image {
  height: 200px;
  /* overflow: hidden; */
  width: 100%;
}

.results-list__item__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: relative;
  top: 0;
  left: 0;
  border: 1px solid white;
}

h3 {
  font-weight: bold;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

